import React, { Component } from "react";
import { connect } from "react-redux";
import {ConvForm} from "components"
import springPIC from 'assets/images/spring.jpg'; 
import summerPIC from 'assets/images/summer.jpg'; 
import autumnPIC from 'assets/images/autumn.jpg'; 
import winterPIC from 'assets/images/winter.jpg'; 

class Survey extends Component<{}> {
  componentDidMount() {
    //     
  }

  render() {
    return (
        <ConvForm formFields={[
          {
            'tag': 'cf-robot-message',
            'cf-questions': '.   .'
          },
          {
            'tag': 'cf-robot-message',
            'cf-questions': '      !'
          },
          {
            'tag': 'select',
            'name': 'gender',
            'cf-questions': '   ?',
            "isMultiChoice": false, 
            "children": [
              {
                'tag': 'option',
                'cf-label': '   .',
                'value': 0
              },
              {
                'tag': 'option',
                'cf-label': '   .',
                'value': 1
              },
              {
                'tag': 'option',
                'cf-label': '     .',
                'value': 2
              }
            ]
          },
          {
            'tag': 'cf-robot-message',
            'name': 'if-male',
            'cf-conditional-gender': 0,
            'cf-questions': '  !'
          },
          {
            'tag': 'cf-robot-message',
            'name': 'if-female',
            'cf-conditional-gender': 1,
            'cf-questions': '  ?'
          },
          {
            'tag': 'cf-robot-message',
            'name': 'if-unisex',
            'cf-conditional-gender': 2,
            'cf-questions': '   !'
          },
          {
            'tag': 'cf-robot-message',
            'cf-questions': '  ,'
          },
          {
            'tag': 'input',
            'type': 'number',
            'name': 'age',
            'min': 10,
            'max': 99,
            'cf-questions': '   ?',
            'cf-error': '   ',
            'cf-input-placeholder': '  . (10 ~ 99)'
          },
          {
            'tag': 'cf-robot-message',
            'cf-questions': '{previous-answer} ?   ...'
          },
          {
            "tag": "fieldset",
            "type": "Checkboxes",
            'cf-input-placeholder': '  .',
            'cf-questions': '    !',
            'children': [
              {
                'tag':'input',
                'type': 'checkbox',
                'name': 'seasons',
                'cf-label': '',
                'value': 1,
                'cf-image': springPIC
              },
              {
                'tag': 'input',
                'type': 'checkbox',
                'name': 'seasons',
                'cf-label': '',
                'value': 2,
                'cf-image': summerPIC
              },
              {
                'tag': 'input',
                'type': 'checkbox',
                'name': 'seasons',
                'cf-label': '',
                'value': 3,
                'cf-image': autumnPIC
              },
              {
                'tag': 'input',
                'type': 'checkbox',
                'name': 'seasons',
                'cf-label': '',
                'value': 4,
                'cf-image': winterPIC
              }
            ]
          },
          {
            'tag': 'cf-robot-message',
            'cf-questions': '...    !'
          },
          {
            'tag': 'select',
            'name': 'include',
            'cf-questions': '  3  !',
            'cf-validation': 'isIncludeValid',
            'multiple': true,
            'children': [
              {
                'tag': 'option',
                'cf-label': '',
                'value': 1
              },
              {
                'tag': 'option',
                'cf-label': '   ',
                'value': 2
              },
              {
                'tag': 'option',
                'cf-label': '  ',
                'value': 3
              },
              {
                'tag': 'option',
                'cf-label': '  ',
                'value': 4
              },
              {
                'tag': 'option',
                'cf-label': '  ',
                'value': 5
              },
              {
                'tag': 'option',
                'cf-label': '',
                'value': 6
              },
              {
                'tag': 'option',
                'cf-label': ', ',
                'value': 7
              },
              {
                'tag': 'option',
                'cf-label': ',  ',
                'value': 8
              },
              {
                'tag': 'option',
                'cf-label': ',  ',
                'value': 9
              },
              {
                'tag': 'option',
                'cf-value': ', ,  ',
                'value': 10
              },
              {
                'tag': 'option',
                'cf-value': ', ',
                'value': 11
              },
              {
                'tag': 'option',
                'cf-value': '',
                'value': 12
              }
            ]
          }


        ]} />
    );
  }
}

export default connect(
  (state) => ({
    //      ?
  }),
  (dispatch) => ({
    //     
  })
)(Survey);
