import { createAction, handleActions } from 'redux-actions';

import { Map } from 'immutable';
import * as API from 'lib/api';
import { pender } from 'redux-pender';

const SET_LOGGED_INFO = 'user/SET_LOGGED_INFO'; //   
const SET_VALIDATED = 'user/SET_VALIDATED'; // validated  
const LOGOUT = 'user/LOGOUT'; // 
const CHECK_STATUS = 'user/CHECK_STATUS'; //   

export const setLoggedInfo = createAction(SET_LOGGED_INFO); // loggedInfo
export const setValidated = createAction(SET_VALIDATED); // validated
export const logout = createAction(LOGOUT, API.logout);
export const checkStatus = createAction(CHECK_STATUS, API.checkStatus);

const initialState = Map({
    loggedInfo: Map({ //    
        token: 'no token',
        user: null
    }),
    logged: false, //   
    validated: false //         
});

export default handleActions({
    [SET_LOGGED_INFO]: (state = initialState, action) => state.set('loggedInfo', Map(action.payload)).set('logged', true),
    [SET_VALIDATED]: (state:Map<any, any>, action) => state.set('validated', action.payload),
    ...pender({
        type: CHECK_STATUS,
        onSuccess: (state:Map<any, any>, action) => state.set('loggedInfo', Map(action.payload.data)).set('validated', true), 
        onFailure: (state:Map<any, any>, action) => initialState
    })
}, initialState);